* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  overflow: hidden;
}

:root {
  --little-gray: #007bff;
  --more-gray: #222222;
}

#introHolder {
  height: 100%;
  width: 100%;
  position: absolute;
}

.introHeaders {
  text-align: center;
  z-index: 10;
}

.introHeaderAfterStart {
  position: absolute;
  top: 50vh;
  left: 50vw;
  transform: translate(-50%, -50%);
  background: green;
  transition: all 2s;
  z-index: 10;
  padding-top: 105%;
  border-radius: 50%;
  height: 0;
  width: 0;
}

.removeFromDOM {
  pointer-events: none;
}

#HomeContentHolder {
  display: flex;
  gap: 0 1vh;
  height: 100%;
}

#HomeNavigation,
#HomeMusicList {
  background: #080808;
  height: 99%;
  color: white;
  position: relative;
}

#player {
  position: fixed;
  bottom: 1vh;
  width: 85%;
  height: 10vh;
  background: #222222;
  z-index: 10;
  opacity: 0.9;
  border-radius: 5px;
  display: flex;
  justify-content: space-between;
  color: white;
  min-height: 50px;
}

#player_image {
  height: 100%;
  width: 12%;
  display: flex;
  align-items: center;
  justify-content: center;
  border: 2px solid;
}

#player_image > img {
  height: 95%;
  width: 95%;
}

#player_details {
  width: 87%;
  height: 100%;
}

#player_details > p {
  height: 40%;
  padding-left: 1vw;
}

audio {
  height: 60%;
  width: 100%;
  border: unset;
}

audio::-webkit-media-controls-play-button {
  background-color: white;
  border-radius: 50%;
  height: 95%;
  margin: 0 0.3vw;
}

audio::-webkit-media-controls-panel {
  background-color: #222222;
}

audio::-webkit-media-controls-current-time-display {
  color: white;
  margin: 0 0.3vw;
}

audio::-webkit-media-controls-time-remaining-display {
  display: none;
}

audio::-webkit-media-controls-timeline {
  padding: 1vh;
  background-color: white;
  border-radius: 25px;
  margin: 0 0.3vw;
}

audio::-webkit-media-controls-mute-button {
  background-color: white;
  height: 95%;
  border-radius: 50%;
  margin: 0 0.3vw;
}

audio::-webkit-media-controls-volume-slider {
  padding: 1vh;
  background-color: white;
  border-radius: 25px;
}

audio::-webkit-media-controls-options {
  display: none;
}

#HomeNavigation {
  width: 20%;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 7vh 0;
  padding-top: 16vh;
  z-index: 5;
}

#HomeMusicList {
  width: 80%;
}

#HomeContentHolder #homeGreet {
  text-align: right;
  margin: 1vh 0;
  margin-right: 5vw;
}

#Homenavigation > button {
  background: var(--more-gray);
  text-align: center;
  position: relative;
  color: white;
  border-radius: 5%;
  border: unset;
  color: rgba(255, 255, 255, 0.5);
}

button:hover {
  color: rgba(255, 255, 255, 1);
}

button > h4 {
  margin-top: 20%;
  transform: translateY(-50%);
}

#outerContainer {
  height: 0.5vh;
  margin-bottom: 1vh;
}

#innerContainer {
  margin-left: 50%;
  transform: translate(-50%, 0);
  height: 100%;
  width: 0%;
  background: green;
  animation: example 4s linear 2s infinite alternate;
}

@keyframes example {
  from {
    width: 0%;
  }
  to {
    width: 100%;
  }
}

input[type="text"] {
  padding: 0.5vh 3vw;
  border-radius: 99px;
  width: 75%;
  margin-top: 2%;
  margin-left: 50%;
  transform: translateX(-50%);
  font-size: 2.6vh;
}

input[type="button"] {
  border-radius: 99px;
  color: white;
  width: 25%;
  min-width: fit-content;
  padding: 1vh 2vw;
  margin-top: 4%;
  margin-left: 50%;
  transform: translateX(-50%);
  font-size: 2.2vh;
}

input[type="text"]:focus {
  box-shadow: 1px 1px 1px 1px forestgreen;
}

#searchHeader {
  margin-top: 10%;
  text-align: center;
}

#typesDiv {
  height: 20vh;
  width: fit-content;
}

.typeButtons {
  padding: 5vh 10vh;
}

.songCategories {
  max-width: 220px;
  max-height: 420px;
  height: 30vh;
  width: 27vw;
  border-radius: 5%;
  border: 1px solid white;
  position: relative;
  z-index: 1;
}

.songCategories > img {
  height: 75%;
  width: 100%;
}

.songCategories > div {
  height: 22%;
  background-color: #1c1c1c;
  display: flex;
  justify-content: space-between;
  align-items: center;
  overflow: visible;
  padding-right: 0.5vw;
}

.songCategories > div > #playButton {
  background-color: green;
  border-radius: 99px;
  height: 5vh;
  width: 5vh;
  display: flex;
  align-items: center;
  justify-content: center;
  transform: translate(20%, -45%);
}

#types-button-container {
  width: 100%;
  background: #080808;
  white-space: nowrap;
  overflow: hidden;
  position: relative;
  user-select: none;
  cursor: pointer;
}

#inner-container {
  width: fit-content;
  background: #121212;
  padding: 0.5vh;
  transition: all 0.5s;
}

.inline-button {
  background: #222222;
  border-radius: 99px;
  width: fit-content;
  margin: 1vw;
  display: inline-block; /* Makes buttons inline */
  padding: 1vh 5vw; /* Adds padding to buttons */
}

#container {
  width: 12vh;
  height: 10vh;
  background-color: red;
}

.NavigationButtons {
  color: rgba(256, 256, 256, 0.4);
  background: inherit;
  border: unset;
}

.divInMusicContent {
  padding: 1.5vh;
  width: 100%;
}

.showingMusicList {
  margin-top: 2vh;
  display: flex;
  justify-content: space-evenly;
  gap: 2vh 1vh;
  flex-wrap: wrap;
}

/* controls the screen styling for screeen widths less than 450px */
@media screen and (max-width: 450px) {
  /* removing the display property so that the navigation bar will come down */
  #HomeContentHolder {
    display: unset;
  }

  /* resetting the width so that they can take the full width of the screen */
  #HomeNavigation,
  #HomeMusicList {
    margin: 0;
    width: 100%;
  }

  #HomeNavigation {
    position: absolute;
    height: 10%;
    bottom: 0px;
    border-radius: 5%;
    justify-content: space-evenly;
    flex-direction: row;
    gap: 0;
    /*unset padding to clear the padding of big screen more 450 px*/
    padding: 0;
  }

  #HomeMusicList {
    height: 89%;
  }

  #player {
    bottom: 10.5%;
    height: 8vh;
    width: 95%;
    left: 2.5%;
  }

  #player_image {
    width: 20%;
  }

  #player_details {
    width: 79%;
  }

  button {
    background: #222222;
    height: fit-content;
    width: 25%;
    text-align: center;
    position: relative;
    border-radius: 5%;
    border: unset;
  }

  button > h4 {
    margin-top: 10%;
    transform: translateY(-50%);
  }

  #introHeader {
    padding-top: 80%;
  }

  .songCategories {
    height: 22vh;
  }
}

@media screen and (min-width: 720px) {
  #HomeNavigation {
    width: 12%;
  }

  #HomeMusicList {
    width: 88%;
  }
}

#musicListHolderListView {
  height: 100vh;
  color: white;
  overflow-y: auto;
  position: relative;
}

#musicListHolderListView > #singerHeader {
  height: 40vh;
  background: rgb(0, 19, 0);
  background: linear-gradient(
    114deg,
    rgba(0, 19, 0, 1) 6%,
    rgba(2, 159, 2, 1) 100%
  );
  margin-bottom: 1vh;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-flow: column;
}

#musicListHolderListView > #singerHeader > img {
  height: 26vh;
  width: 26vh;
  border-radius: 100px;
  margin-bottom: 1vh;
}

/*  a will only be visible when you are inspecting the page */
/* cause Link is React defined to use in react code in place of a tag of html */
#musicListHolderListView > #singerHeader > a > #goBack {
  height: 6.5vh;
  border-radius: 100px;
  width: 6.5vh;
  background-color: rgba(255, 255, 255, 0.35);
  position: absolute;
  top: 3.5vh;
  left: 3.5vh;
  display: flex;
  justify-content: center;
  align-items: center;
  color: white;
}

#musicListHolderListView > #musicList {
  padding: 1vh;
  padding-bottom: 5vh;
  /* DO nothing */
}

#musicListHolderListView > #musicList > .track {
  height: 11vh;
  margin: 1vh;
  background-color: rgba(0, 0, 0, 0.7);
  display: flex;
  align-items: center;
  gap: 2vh;
  padding: 1vh;
}

#musicListHolderListView > #musicList > .track > img {
  height: 100%;
  width: 22%;
}

#musicListHolderListView > #musicList > .track > .trackNameContainer {
  width: 68%;
}
