* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  height: -webkit-fill-available;
}

body {
  height: -webkit-fill-available;
  background: #1c1c1c;
}

.App {
  font-family: sans-serif;
  text-align: center;
  position: relative;
}

#root {
  height: 100vh;
}

.searchedTrack {
  height: fit-content;
  margin: 1.5vh;
  display: flex;
  align-items: center;
  font-size: 2vh;
  background-color: #222222;
}

.searchedTrack > .trackInfo > .trackName {
  font-weight: bold;
  margin-bottom: 0.5vh;
}

.searchedTrack > img {
  height: 12vh;
  min-width: 12vh;
  margin-right: 1.5vw;
}

#musicPlayerHolder {
  height: 12vh;
  display: flex;
  align-items: center;
}

#musicPlayerHolder > img {
  height: 100%;
  width: 12vh;
}

#musicPlayer {
  width: 75%; /* Set the width of the audio player */
  height: 60%; /* Set the height of the audio player */
}

#albumHolder {
  height: 100vh;
  background-color: black;
  overflow-y: auto;
}

#tracksListHolder {
  height: fit-content;
  padding: 1vh;
}

#goBack_albumDetails {
  height: 6.5vh;
  border-radius: 100px;
  width: 6.5vh;
  background-color: rgba(255, 255, 255, 0.35);
  position: absolute;
  top: 3.5vh;
  left: 3.5vh;
  display: flex;
  justify-content: center;
  align-items: center;
  color: white;
}

#singerHeader_albumDetails {
  /* height: 40vh; */
  min-height: fit-content;
  background: rgb(0, 19, 0);
  background: linear-gradient(
    114deg,
    rgba(0, 19, 0, 1) 10%,
    rgba(2, 159, 2, 1) 110%
  );
  margin-bottom: 1vh;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-flow: column;
  padding: 2vh;
}

#singerHeader_albumDetails > img {
  height: 23vh;
  width: 23vh;
  border-radius: 1px;
  margin-bottom: 1vh;
}

.TrackOfAlbum {
  height: fit-content;
  background-color: #222222;
  margin: 1vh;
  padding: 1vh 1.5vw;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
